export default function CongressLearningPage() {
  return (
    <div className="flex flex-col gap-6">
      <div>
        <h1 className="text-3xl font-bold font-headline">Student Congress Resources</h1>
        <p className="text-muted-foreground">
          Learn about parliamentary procedure, legislation, and debate strategy.
        </p>
      </div>

      <div className="grid gap-6 md:grid-cols-2 lg:grid-cols-3">
        {congressResources.map((resource, index) =>
          resource.type === 'website' ? (
            <WebsiteResourceCard key={index} resource={resource} />
          ) : (
            <VideoResourceCard key={index} resource={resource} />
          )
        )}
      </div>
    </div>
  );
}
