
'use client';

import * as React from 'react';
import { Card, CardHeader, CardTitle, CardDescription, CardContent } from '@/components/ui/card';
import { Button } from '@/components/ui/button';
import { ExternalLink } from 'lucide-react';
import type { WebsiteResource, VideoResource } from '@/lib/learning-resources';

export function WebsiteResourceCard({ resource }: { resource: WebsiteResource }) {
  return (
    <Card>
      <CardHeader>
        <CardTitle className="text-lg">{resource.title}</CardTitle>
        <CardDescription>{resource.description}</CardDescription>
      </CardHeader>
      <CardContent>
        <Button asChild variant="outline" size="sm">
          <a href={resource.url} target="_blank" rel="noopener noreferrer">
            <ExternalLink className="mr-2" />
            Visit Site
          </a>
        </Button>
      </CardContent>
    </Card>
  );
}

export function VideoResourceCard({ resource }: { resource: VideoResource }) {
  const embedUrl = `https://www.youtube.com/embed/${resource.videoId}`;
  return (
    <Card>
      <CardHeader>
        <CardTitle className="text-lg">{resource.title}</CardTitle>
        <CardDescription>{resource.description}</CardDescription>
      </CardHeader>
      <CardContent>
        <div className="aspect-video w-full rounded-md overflow-hidden border">
          <iframe
            width="100%"
            height="100%"
            src={embedUrl}
            title={resource.title}
            allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture"
            allowFullScreen
          ></iframe>
        </div>
      </CardContent>
    </Card>
  );
}
